body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  position: relative;
  background-color: #343434;
  min-height: 100vh;
  width: 100%; }

* {
  margin: 0;
  padding: 0; }

html {
  font-family: "Raleway", sans-serif; }

.header {
  margin-bottom: 20px;
  color: white; }

.game {
  padding: 20px;
  background-color: #121212;
  margin-right: -75px;
  border-radius: 4px; }

.display {
  background-color: #00761f;
  padding-left: 40px;
  border: 2px solid black; }

.board {
  background-color: #00761f;
  position: relative;
  padding: 20px 20px 20px 60px;
  border-radius: 4px; }

body {
  font-size: 18px; }

.button {
  border-radius: 5px;
  border: 2px solid #d4d4d4;
  background-image: linear-gradient(to right bottom, rgba(67, 67, 67, 0.8), rgba(57, 57, 57, 0.8));
  padding: 15px 30px;
  margin: 0px 10px;
  color: white;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 22px;
  -webkit-transform: translate(-1px);
          transform: translate(-1px);
  cursor: pointer; }

.button:hover {
  -webkit-transform: translateY(-2px);
          transform: translateY(-2px); }

.button:active {
  -webkit-transform: translate(0);
          transform: translate(0); }

.stats__panel {
  display: flex;
  justify-content: center;
  height: 80px;
  padding-top: 20px;
  padding-right: 40px; }

.stats__label {
  color: #171717;
  font-size: 24px; }
  .stats__label--player {
    line-height: 40px; }

.stats__display {
  width: 33%;
  font-weight: bold;
  border: 2px solid white; }

.stats__spin {
  width: 33%;
  font-weight: bold;
  border: 2px solid white;
  border-left: none; }
  .stats__spin--label {
    display: flex;
    justify-content: center;
    margin-bottom: 5px; }

.stats__amount--label {
  font-size: 20px;
  font-weight: 700;
  margin-left: 5px; }

.stats__result {
  width: 33%;
  border: 2px solid white;
  border-left: none;
  font-size: 12px; }
  .stats__result--display {
    font-size: 26px; }
  .stats__result--label {
    font-size: 20px;
    font-weight: bold;
    color: white; }
  .stats__result--message {
    color: white; }
  .stats__result--amount {
    color: #121212;
    font-size: 24px;
    background-color: white;
    border-radius: 2px;
    padding: 0 6px; }

.spin-result {
  color: white;
  width: 50%;
  height: 40px;
  margin: 0 auto; }

.result__green {
  color: #00761f; }

.result__red {
  color: #ac1909; }

.result__black {
  color: #030303; }

.top-row {
  display: flex;
  justify-content: center; }

.middle-row {
  display: flex;
  justify-content: center; }

.bottom-row {
  display: flex;
  justify-content: center; }

.row-bet {
  width: 80px;
  height: 80px;
  border: 1px solid white;
  background-color: #00761f;
  color: white;
  cursor: pointer; }

.zero__bet {
  width: 80px;
  height: 245px;
  background-color: #00761f;
  position: absolute;
  left: -81px;
  border-top-left-radius: 50px;
  border-bottom-left-radius: 50px;
  color: white;
  line-height: 240px;
  border: 1px solid white;
  cursor: pointer; }

.side-line-1 {
  display: flex; }

.side-line-2 {
  display: flex; }

.board__chips {
  position: relative; }

.thirds-bet {
  width: 206px;
  height: 40px;
  border: 1px solid white;
  background-color: #00761f;
  color: white;
  display: flex;
  justify-content: center;
  cursor: pointer; }
  .thirds-bet__height {
    line-height: 40px;
    margin-right: 10px; }

.one-of-two-bet {
  width: 102px;
  height: 40px;
  border: 1px solid white;
  background-color: #00761f;
  color: white;
  display: flex;
  justify-content: center;
  cursor: pointer; }
  .one-of-two-bet__height {
    line-height: 40px;
    margin-right: 10px; }

.board-number {
  width: 50px;
  height: 80px;
  border: 1px solid white;
  align-self: center;
  color: white;
  cursor: pointer; }

.red {
  background-color: #ac1909; }

.black {
  background-color: #030303; }

.selected {
  background-color: blue; }

.green {
  background-color: #00761f; }

/* betting */
.bet__options {
  display: flex;
  justify-content: center;
  margin-top: 10px;
  height: 100px;
  margin-bottom: 10px; }
  .bet__options--label {
    color: white;
    line-height: 80px;
    font-size: 22px;
    margin-right: 10px; }

.bet__image {
  padding: 10px;
  width: 75px;
  cursor: pointer; }

.bet__chips {
  cursor: pointer; }

.bet__selected {
  border: 2px solid #d4d4d4;
  border-radius: 4px;
  cursor: pointer; }

.chip__small {
  width: 40px;
  height: 40px; }

.screen {
  display: flex;
  justify-content: center; }

.wheel__image {
  width: 500px;
  height: 500px;
  margin-left: 100px; }

.wheel__display {
  position: relative; }

